.alg-wc-cp-list .product-thumbnail img {
  max-width: 70px;
  margin: 0 auto 5px auto;
}

.alg-wc-cp-list .added_to_cart {
  display: none;
}

.alg-wc-cp-list td, .alg-wc-cp-list tr {
  vertical-align: middle !important;
  text-align: center !important;
}

.alg-wc-cp-list th {
  text-align: center;
}

.alg-wc-cp-list tr:nth-child(even) td {
  background: #F7F7F7;
}

.alg-wc-cp-list tr:nth-child(odd) td {
  background: #ffffff;
}

.alg-wc-cp-list .product-name {
  line-height: 18px;
}

.alg-wc-cp-list .add-to-cart-btn a {
  line-height: 18px;
}

@media (max-width: 768px) {
  .alg-wc-cp-list.responsive td, .alg-wc-cp-list.responsive th {
    padding: 20px 5px;
  }
  .alg-wc-cp-list.responsive .add-to-cart-btn {
    line-height: 15px;
  }
  .alg-wc-cp-list.responsive .add-to-cart-btn a {
    padding: 5px 10px;
  }
  .alg-wc-cp-list.responsive .product-price del, .alg-wc-cp-list.responsive .product-price ins {
    display: block;
  }
  .alg-wc-cp-list.responsive .product-price ins > span {
    display: block;
  }
}

.iziModal .iziModal-header-subtitle, .iziModal .iziModal-header-title {
  clear: none;
}

.iziModal {
  outline: none;
}
